
        body {
            background-image: url('/stad.jpg');
            background-size: cover;
            background-repeat: no-repeat;
            background-attachment: fixed;
            background-position: center;
            color: #000;
            overflow-x: hidden;
            display: flex;
            flex-direction: column;
            height: 100vh;
        }

        .main-content {
            flex: 1;
        }
        #username {
    background: rgba(255, 255, 255, 0.7); /* Slightly transparent white */
            backdrop-filter: blur(5px);
            color: rgb(117, 31, 31);
    padding: 2px 5px; /* Add some padding */
    border-radius: 5px; /* Optional: Add rounded corners */
}

/* Add some margin between navbar items */
.navbar-nav .nav-item {
    margin-right: 15px; /* Adjust the value as needed */
}
/* Ensure the dropdown menu appears above other content */
.navbar .dropdown-menu {
    z-index: 1050; /* Higher than most elements */
}

/* Ensure the parent container has a higher stacking context */

.navbar .dropdown-item {
    background: rgba(175, 21, 21, 0.7); /* Slightly transparent red */
            backdrop-filter: blur(5px);
            color: white;
}

        .navbar-scroll .nav-link,
        .navbar-scroll .navbar-toggler-icon,
        .navbar-scroll .navbar-brand {
            color: #fff;
        }

        .navbar-scroll,
        .navbar-scrolled {
            background-color: #cbbcb1;
        }

        .mask-custom {
            backdrop-filter: blur(5px);
            background-color: rgba(255, 255, 255, .15);
        }

        .navbar-brand {
            font-size: 1.75rem;
            letter-spacing: 3px;
        }

        .matches-container {
            margin-top: 80px;
        }

        .table {
            width: 100%;
            table-layout: auto;
            margin-top: 100px;
        }

        .table-responsive {
            overflow-x: auto;
            -webkit-overflow-scrolling: touch;
        }

        .table th, .table td {
            text-align: center;
            padding: 10px;
            font-size: 1rem;
            color: #212529;
            background: rgba(255, 255, 255, 0.7);
            backdrop-filter: blur(5px);
        }

        .table th {
            color: #f8f9fa;
            background: rgba(203, 188, 177, 0.7);
            position: sticky;
            top: 0;
        }

        .table tr:hover {
            background-color: #dee2e6;
            cursor: pointer;
        }

        .team-logo {
            width: 50px;
            height: 50px;
            object-fit: contain;
            margin-right: 30px;
            border: none;
            background: none;
        }

        .table .btn-danger {
            font-size: 0.8rem;
            background: linear-gradient(to right, #ff4e50, #f7b42c);
            padding: 5px 10px;
            border-radius: 5px;
        }

        .table .btn-danger:hover {
            background: linear-gradient(to right, #ff0000, #ff8000);
            color: #fff;
        }

        footer {
            padding: 10px 0;
            text-align: center;
            color: white;
            width: 100%;
            margin-top: auto;
        }

        .modal-content {
            border-radius: 20px;
            background: linear-gradient(to bottom right, #ffffff, #f8f9fa);
            box-shadow: 0 8px 15px rgba(0, 0, 0, 0.3);
            overflow: hidden;
        }

        @media (max-width: 768px) {
            .navbar {
                position: fixed;
                z-index: 1020;
                width: 100%;
            }

            .matches-container {
                margin-top: 120px;
            }

            .table th, .table td {
                padding: 0.5rem;
                font-size: 0.85rem;
            }

            #addMatchButton {
                font-size: 0.9rem;
                padding: 8px 16px;
            }

            .navbar-brand {
                font-size: 1.2rem;
            }

            .modal-header {
                font-size: 1.1rem;
            }

            .modal-body .form-control {
                font-size: 0.9rem;
            }

            .btn-primary, #addScorerBtn {
                font-size: 0.9rem;
            }
        }
    